import React from 'react';
import '../styles/MarketTable.css';

const MarketTable = () => {
  const markets = [
    { pair: 'BTC/USDT', price: '58,000', change: '+2.3%' },
    { pair: 'ETH/USDT', price: '4,000', change: '+1.8%' },
    { pair: 'BNB/USDT', price: '500', change: '-0.5%' },
  ];

  return (
    <table className="market-table">
      <thead>
        <tr>
          <th>Pair</th>
          <th>Last Price</th>
          <th>24h Change</th>
        </tr>
      </thead>
      <tbody>
        {markets.map((market, index) => (
          <tr key={index}>
            <td>{market.pair}</td>
            <td>{market.price}</td>
            <td>{market.change}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default MarketTable;
