import React from 'react';
import { Line } from 'react-chartjs-2';
import '../styles/TradingChart.css';

const TradingChart = () => {
  const data = {
    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
    datasets: [
      {
        label: 'BTC/USDT',
        data: [32000, 35000, 40000, 45000, 48000, 50000, 58000],
        borderColor: '#fcd535',
        backgroundColor: 'rgba(252, 213, 53, 0.2)',
        fill: true,
        tension: 0.3,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: 'top',
      },
    },
    scales: {
      x: {
        grid: {
          display: false,
        },
      },
      y: {
        ticks: {
          callback: (value) => `$${value}`,
        },
        grid: {
          color: '#2c2f36',
        },
      },
    },
  };

  return (
    <div className="trading-chart">
      <h2>Trading Chart</h2>
      <Line data={data} options={options} />
    </div>
  );
};

export default TradingChart;
